.button{position:relative;border:none;font-family:var(--font-family);font-size:16px;font-weight:500;line-height:1.4;border-radius:var(--border-radius);cursor:pointer;display:flex;justify-content:center;align-items:center;outline:none;padding:0 20px;height:50px;border:1px solid transparent}.button[disabled]{opacity:0.65;cursor:default}.icon{flex-shrink:0;display:inline-flex;justify-content:center;align-items:center}.icon-withMargin{margin-right:0.5rem}.design-primary{color:white;background-color:var(--color-brand)}.design-primary:hover:not([disabled]){background-color:var(--color-brand-hover)}.design-primary:active:not([disabled]){background:var(--color-brand-active)}.design-primary[disabled],.design-primary[disabled]:hover{background:var(--color-brand)}.design-primary-light{color:var(--color-brand);background-color:rgba(255,0,44,0.07)}.design-primary-light:hover:not([disabled]){background-color:rgba(255,0,44,0.15)}.design-primary-light:active:not([disabled]){background:rgba(255,0,44,0.25)}.design-primary-light[disabled],.design-primary-light[disabled]:hover{border-color:rgba(255,0,44,0.25)}.design-light{background:var(--light-blue);color:var(--color-text);font-weight:600;background-color:#fff}.design-light:hover:not([disabled]){background:var(--light-blue-hover)}.design-light:active:not([disabled]){background:var(--light-blue-active)}.design-light[disabled],.design-light[disabled]:hover{background:var(--color-border)}.design-secondary{background:var(--color-lightGrey);color:var(--color-text);font-weight:600}.design-secondary:hover{background:var(--color-mediumGrey)}.design-secondary:active{background:var(--color-border)}.design-secondary[disabled],.design-secondary[disabled]:hover{background:var(--color-border)}.design-fb{background:var(--color-facebook);color:#fff}.design-fb:hover:not([disabled]){background-color:var(--color-fb-hover)}.design-fb:active:not([disabled]){background-color:var(--color-fb-active)}.design-yandex{background:var(--color-yandex);color:#000}.design-yandex:hover:not([disabled]){background-color:var(--color-yandex-hover)}.design-yandex:active:not([disabled]){background-color:var(--color-yandex-active)}.design-vk{background-color:var(--color-vk);color:#fff}.design-vk:hover:not([disabled]){background-color:var(--color-vk-hover)}.design-vk:active:not([disabled]){background-color:var(--color-vk-active)}.size-small{height:40px;font-weight:600}.size-default{padding:11px 20px}.size-large{font-size:30px;font-weight:500;padding:24px;height:80px}.block{width:100%}.fit{padding-right:0;padding-left:0}
