.dropdown-area{position:absolute;box-shadow:0px 10px 40px rgba(0,0,0,0.25);opacity:0;visibility:hidden;transform:translate3d(0, 30px, 0);display:flex;justify-content:center;flex-direction:column;background:#fff;border-radius:var(--border-radius);padding:10px 0;max-width:450px;min-width:300px;font-family:var(--font-family);z-index:100}.dropdown-area:before{border:solid white;border-width:9px 9px 0 0;transform:rotate(-45deg);display:inline-block;padding:3px;background:#fff;content:'';position:absolute}.dropdown-area.position-bottom-right{top:100%;right:0}.dropdown-area.position-bottom-right:before{top:-8px;right:40px}.dropdown-area.position-bottom-left{top:100%;left:0}.dropdown-area.position-bottom-left:before{top:-8px;left:40px}.dropdown-area.position-top-left{bottom:calc(100% + 40px);left:0}.dropdown-area.position-top-left:before{bottom:-8px;left:40px}.dropdown-area.position-top-right{bottom:calc(100% + 40px);right:0}.dropdown-area.position-top-right:before{bottom:-8px;right:40px}.animated{transition:transform 0.15s, opacity 0.15s, visibility 0.15s}.opened{transform:translate3d(0, 20px, 0);opacity:1;visibility:visible}.horizontal-left:before{left:50px}.horizontal-right:before{right:50px}
